import { useState, useEffect } from "react";


function Movie(){

  /*
  * use state and Effect
  */
  const [inputs, setInputs] = useState({
    name: '',
    nickname: ''
  });

  useEffect(() =>{
  
  },[])//[]  useState      

  /*
  * User Function
  */
  const { name, nickname } = inputs; //     

  const onChange = (e) => {
    console.log(e.target);
    const { value, name } = e.target; //  e.target  name  value  

    console.log(value);
    setInputs({
      ...inputs, //  input   (this.super)
      [name]: value // name    value  
    });
  };

  const onReset = () => {
    setInputs({
      name: '',
      nickname: '',
    })
  };

  /*
  * HTML {javascript}
  */
  return(
    <div>
      <input name="name" placeholder="" onChange={onChange} value={name} />
      <input name="nickname" placeholder="" onChange={onChange} value={nickname}/>
      <button onClick={onReset}></button>
      <div>
        <b>: </b>
        {name} ({nickname})
      </div>
    </div>
  )
}

export default Movie;